
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {loginForm,loginCheck} from 'src/service/api';
import {setStore,getStore} from 'src/config/mUtils'

export default {
components:{
XHeader,
},
data(){
return {
borderColor: {
borderColor: '#333'
},
ruleForm: {
username: '', //用户名
password: '' 
},
registration_data:{
pwdType:"password",
src:require("../../images/login_kan1.png")
}
}
},
methods:{
back(){
this.$router.go(-1);
},
findPwd(){
this.$router.push('/findPwd')
},
changeType(){
this.registration_data.pwdType = this.registration_data.pwdType==='password'?'text':'password';
this.registration_data.src=this.registration_data.src==require("../../images/login_kan1.png")?require("../../images/login_kan2.png"):require("../../images/login_kan1.png");
},

login(formName) {

let name=this.ruleForm.username;

let pass=this.ruleForm.password;
if(name==''||name==null){
alert("请输入正确的手机号");
return
}
else if(pass==''||pass==null) {
alert("请输入正确的密码");
return
}

loginForm({username:name,password:pass}).then(res=>{
let data=res.data;
console.log(res);
if(res.code == 0){
this.$vux.toast.show({
text: res.msg,
type:'text',
position: 'middle'
})
}else if(res.code == 1){
this.$vux.toast.show({
text: '登录成功',
type:'text',
position: 'middle'
})
setStore("user",name)
setTimeout(()=>{
this.$router.push("/personFile");
},1000)
}
});
}
}
}




