
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {newsDetails} from 'src/service/api'
import {formatDate} from 'src/assets/js/time'

export default {
components: {
XHeader,
Flexbox,
FlexboxItem
},
data () {
return {
listText:[],
post_content:'',
borderColor: {
borderColor: '#333'
},
}
},
filters: {
formatDate(time) {
var date = new Date(time * 1000);
return formatDate(date, 'yyyy年MM月dd日');
},
},
methods:{
back(){
this.$router.go(-1);
},
},
computed:{
},
mounted(){
newsDetails({id:this.$route.query.id}).then(res => {
this.time=res.time;
this.listText = res.data.article;
this.post_content = res.data.article.post_content.replace(/&lt;/g, "<").
replace(/&quot;/g,'"').
replace(/&gt;/g, ">");
console.log(this.listText);

});

}
}



