import {setStore,getStore} from 'src/config/mUtils'
export default {
components:{
XHeader,
Flexbox,
FlexboxItem,
footNav,
cityPicker
},
data(){
return{
borderColor: {
borderColor: '#333'
},
LocationCity:"",
cityPickerShow:false,
}
},
mounted(){
this.LocationCity = getStore("LocationCity");
},
computed: {
},
methods: {
back(){
this.$router.go(-1);
},
gotoAddress(path){
