
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {newsList} from 'src/service/api'
export default {
components: {
XHeader,
Flexbox,
FlexboxItem,
},
data () {
return {
list:[],
borderColor: {
borderColor: '#333'
},
}
},
methods:{
back(){
this.$router.go(-1);
},
},
mounted(){
newsList({id:this.$route.query.id}).then(res => {
this.list = res.data;
console.log(this.list);
})
}
}



