
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {Info} from 'src/service/api'
export default {
name: 'course',
components: {
XHeader,
Tab,
TabItem,
Popup,
XSwitch,
Cell,
Group,
XButton
},
data(){
return{
types:[{id:1,title:'电工作业'},{id:2,title:'制冷与空调作业'},{id:3,title:'高处作业'},{id:4,title:'焊接热切割'},{id:5,title:'电工'}],
showIndex:0,
borderColor: {
borderColor: '#333'
},
infoList:[]
}
},
mounted(){
Info({id:this.$route.query.id}).then(res=>{
this.infoList=res.data.course
console.log(this.infoList);
})
},
methods:{
onItemClick(index){
console.log(index);
this.showIndex = index;
},
back(){
this.$router.go(-1);
},
call(){
window.location.href="tel:400-999-6389"
},
toOrgan(){
this.$router.push("/organ");
}
}
}



