


import XHeader from 'vux/src/components/x-header/index.vue'

import { findPassword, yzmGet } from 'src/service/api'
import {setStore,getStore} from 'src/config/mUtils'

export default {

components: {XHeader},
data() {

return {
borderColor: {
borderColor: '#333'
},
yzmShow:true,
countdown:'',
mobile:'',
code:'',
mobile:'',
password:'',
password2:'',
agreeShow:true,
};
},

computed: {},

watch: {},

methods: {
back(){
this.$router.go(-1);
},
toLogin(){
this.$router.push('/login');
},
agreement(){
this.agreeShow = !this.agreeShow
},
yzm(){
let mobile = this.mobile;
if(mobile == ''){
this.$vux.toast.show({
text: '请输入手机号',
type:'text',
position: 'middle'
})
return
}
console.log(mobile);
yzmGet({mobile:mobile,type:'findpassword'}).then(res=>{
console.log(res);
if(res.code == 1){
this.setTime();
}else if(res.code == 0){
this.$vux.toast.show({
text: res.msg,
type:'text',
position: 'middle'
})
}
});
},
setTime(){
this.yzmShow = false;
this.countdown = 60;
let auth_timer = setInterval(()=>{
this.countdown --;
if(this.countdown <= 0 ){
this.yzmShow = true;
clearInterval(auth_timer);
}
},1000)
},
register(){
let password = this.password;
let password2 = this.password2;
let mobile = this.mobile;
let code = this.code;
if(password != password2){
this.$vux.toast.show({
text: "两次输入密码不一致",
type:'text',
position: 'middle'
})
return
}
if(password.length<6){
this.$vux.toast.show({
text: "密码长度不能小于6位",
type:'text',
position: 'middle'
})
return
}
findPassword({mobile:mobile,code:code,password:password}).then(res=>{
console.log(res);
if(res.code == 1){
this.$vux.toast.show({
text: "修改成功",
type:'text',
position: 'middle'
})
setStore("user",this.mobile);
setTimeout(()=>{
this.$router.push("/personFile");
},1000)
}else {
this.$vux.toast.show({
text: res.msg,
type:'text',
position: 'middle'
})
}
})
}
},

created() {

},

mounted() {

},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}



