
import footNav from 'src/components/footNav'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {loginCheck,logout} from 'src/service/api';
import {setStore,getStore,removeStore} from 'src/config/mUtils'

export default {
data () {
return {
username:"",
}
},
components: {
Flexbox,
FlexboxItem,
Group,
Cell,
footNav
},
methods:{
empty(name){
console.log(111)
this.$router.push({path:'/empty',query:{name:name}})
},
gotoAddress(path){
this.$router.push(path)
},

loginout() {
logout({}).then(res=>{
removeStore("user");
this.$vux.toast.show({
text: '退出成功',
type:'text',
position: 'middle'
}),
setTimeout(() => {
this.$router.push("/home");
}, 1000);
});
},
login(){
loginCheck({}).then(res=>{
console.log(res)
return
})
}
},
mounted() {
this.username  = getStore("user");
console.log(this.username)
},
}



