export default {
  props: {
    value: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      me_checked: this.value
    }
  },
  watch: {
    me_checked(val) {
      this.$emit('input', val);
    }
  },
  methods: {
    toggle() {
      this.me_checked = !this.me_checked;
    }
  }
}



