
import footNav from 'src/components/footNav'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {showlist} from 'src/service/api'
import {formatDate} from 'src/assets/js/time'
import BMap from 'BMap'
import cityPicker from 'src/components/cityPicker'
import {setStore,getStore} from 'src/config/mUtils'

document.addEventListener('plusready', function(a) {
var first = null;
plus.key.addEventListener('backbutton', function() {

if (!first) {
first = new Date().getTime();
console.log('再按一次退出应用');
setTimeout(function() {
first = null;
}, 1000);
} else {
if (new Date().getTime() - first < 1000) {
plus.runtime.quit();
}
}
}, false);
});

export default {
components: {
Tab,
TabItem,
cityPicker,
footNav
},
data () {
return {
active_tab: 'news',
newslist:[],
LocationCity:"定位中",
cityPickerShow:false,
}
},
filters: {
formatDate(time) {
var date = new Date(time * 1000);
return formatDate(date, 'yyyy-MM-dd');
}
},
methods:{
onClick(state){
this.active_tab = state;
},
toCourse(){
this.$router.push("/courseDetail");
},
toCourseList(){
this.$router.push("/courseList");
},
toSearch(){
this.$router.push("/search");
},
gotoAddress(path){
this.$router.push(path)
},
getLocation(){
let _this = this;
const geolocation = new BMap.Geolocation();
geolocation.getCurrentPosition(function getinfo(position){
let city = position.address.city;
_this.LocationCity = city;


}, function(e) {
_this.LocationCity = "定位失败"

}, {provider: 'baidu'});
},
chooCity(){
this.cityPickerShow = !this.cityPickerShow
},
close:function (res){
if(res == 'close'){
this.chooCity();
}
},
formPicker(res){
this.LocationCity = res;
let longitude = getStore("longitude");
let latitude = getStore("latitude");
let da = getStore("type");
},
},
mounted(){
showlist().then(res => {
this.newslist = res.data;
console.log(this.newslist);
});
let LocationCity = getStore("LocationCity");
if(!LocationCity){
this.getLocation();
}else{
this.LocationCity = LocationCity;
}

},
}



