
import {enroll} from 'src/service/api';
import {enrollTwo} from 'src/service/api';
import {enrollThree,yzmGet,apply} from 'src/service/api';
import { getStore, setStore } from 'src/config/mUtils'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XTable,
XButton,
Group,
Popup,
Checklist,
Group,
Cell,
XInput,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
XDialog
},
data () {
return {
username:'',
work3:'',
onplace:'',
companyid:'',
gzcouse:[],
gzcouse1:[],
enroll:[],
gz:false,
fs:false,
jgname:'',
show13:false,
showToast: false,
value1:'1',
value1:'2',
value1:'3',
value1:'4',
commonList: [ '先生', '女士'],
radioValue: ['先生'],
data1:'',
addvalue:false,
active1:'',
active2:'',
active3:'',
truename:'',
mobile:'',
vcode:'',
sex:'',
yzmShow:true,
countdown:'',
agreeShow:true
}
},
mounted(){
this.username=getStore("user");
this.jgname=this.$route.query.name;
},
methods:{
bml(){//加号
this.show13 = true;
this.active1 = '';
this.active2 = '';
this.active3 = '';
this.enrollTwo = [];
this.enrollThree = [];
this.gz = false;
this.fs = false;
this.enrollTwoHid = false;
this.enrollThreeHid = false;
this.companyid=this.$route.query.id;
enroll({companyId:this.$route.query.id}).then(res=>{
this.enroll=res.data;
console.log(this.enroll);
});
},
gzs(data1,name){//一级
this.active1 = name;
enrollTwo({parentId:data1}).then(res=>{
this.enrollTwo=res.data;
this.gz = true;
this.fs = false;
this.$forceUpdate();
console.log(this.enrollTwo);
if(this.enrollTwo.length == 0){
this.enrollTwoHid = true;
}else{
this.enrollTwoHid = false;
}
});
},
fss(data1,data2,data3){//二级

this.active2=data3;
data2=this.companyid;
enrollThree({categoryId:data1,companyId:data2}).then(res=>{
this.enrollThree=res.data;
this.$forceUpdate();
this.fs = true;
console.log(this.enrollThree);
if(this.enrollThree.length == 0){
this.enrollThreeHid = true;
}else{
this.enrollThreeHid = false;
}
});
},
xz(data,price,id){//三级
this.work3 = data;
this.price = price;
this.chooId = id;
this.active3 = data;
},

gzadd(){//确定
this.show13 = false;
let dateId = new Date().getTime();
if(this.enrollTwo.length == 0){
this.$vux.toast.show({
text: '请选择二级分类',
type:'text',
position: 'middle'
})
return
}
console.log(this.enrollThree.length);
if(this.enrollThree.length == 0){
this.$vux.toast.show({
text: '请选择三级分类',
type:'text',
position: 'middle'
})
return
}
for(let key in this.gzcouse){
if(this.chooId == this.gzcouse[key].id){
this.$vux.toast.show({
text: '不能重复选择',
type:'text',
position: 'middle'
})
console.log(this.gzcouse);
return
}
}
this.gzcouse.push({name:this.work3,price:this.price,dateId:dateId,id:this.chooId});
console.log(this.gzcouse);
},

checkTel: function (tel) {
if (/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/.test(tel)) {
return true;
}
return false;
},
change (val, label) {

if(label[0] == "先生"){
this.sex = 'man';
}else{
this.sex = 'woman';
}
},
baoming(){
let truename = this.truename;
let mobile = this.mobile;
let vcode = this.vcode;
let gzcouse = this.gzcouse;
if(gzcouse.length == ""){
this.$vux.toast.show({
text: '请选择工种',
type:'text',
position: 'middle'
})
}else if(truename == ''){
this.$vux.toast.show({
text: '请填写姓名',
type:'text',
position: 'middle'
})
}else if(mobile == ''){
this.$vux.toast.show({
text: '请填写手机号',
type:'text',
position: 'middle'
})
}else if(!this.checkTel(mobile)){
this.$vux.toast.show({
text: '请填写正确手机号',
type:'text',
position: 'middle'
})
}else if(vcode == ''){
this.$vux.toast.show({
text: '请填写验证码',
type:'text',
position: 'middle'
})
}else{
this.showToast = true;
}

},
removecourse(e){
for(let key in this.gzcouse){
if(this.gzcouse[key].dateId == e){
this.gzcouse.splice(key,1);
}
}

},

tijiao(){
let ids = [];
for(let key in this.gzcouse){
ids[key] = this.gzcouse[key].id;
}
console.log(ids);
apply({username:this.username,company_id:this.companyid,truename:this.truename,sex:this.sex,mobile:this.mobile,code:this.vcode,course_ids:ids}).then(res=>{
console.log(res);
let data = res.data;
if(res.code == 0){
this.$vux.toast.show({
text: '网络错误',
type:'text',
position: 'middle'
})
}else if(res.code == 1){
this.$vux.toast.show({
text: '提交成功',
type:'text',
position: 'middle'
})
setTimeout(()=>{
this.$router.push({path:'/step3',query:{order_sn:data.order_sn,jgname:this.jgname}});
const info = {money:data.total_money};
localStorage.setItem('hou', JSON.stringify(info));
},1000)
}
});
},
agreement(){
this.agreeShow = !this.agreeShow
},
yzm(){
let mobile = this.mobile;
yzmGet({mobile:mobile,type:'apply'}).then(res=>{
console.log(res);
if(res.code == 1){
this.setTime();
}
});
},
setTime(){
this.yzmShow = false;
this.countdown = 60;
let auth_timer = setInterval(()=>{
this.countdown --;
if(this.countdown <= 0 ){
this.yzmShow = true;
clearInterval(auth_timer);
}
},1000)
}
},
}



