
import BMap from 'BMap'
import {showlist} from 'src/service/api'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import value2name from 'vux/src/filters/value2name.js'

import { getStore, setStore } from 'src/config/mUtils'
import cityPicker from 'src/components/cityPicker'
export default{
directives: {
TransferDom
},
data() {
return {
exactive:'',
LocationCity:"定位中",
cityPickerShow:false,
title:'',
value_0_1: [],
value: [],
addressData: ChinaAddressV4Data,
showAddress: false,
show13: false,
gzid:'',
wpList: [
{
name: '初训'
},
{
name: '复审'
}
],
active:'初训',
gzlists:[]
}
},
components: {
XAddress,
Popup,
Group,
Cell,
XSwitch,
XAddress,
XButton,
cityPicker
},
created(){
var isopened =getStore("isopened");
if(isopened){
this.$router.replace({path:'/home'});
}else{
setStore("isopened",true);
}
},
methods:{
selected:function(name){//有证无证
console.log(name);
this.active = name;
},
gzselected:function(name){//工种选择
this.exactive = name;
this.gzid=name;
},
choosegz:function(children){
if(children){
this.show13=true
}
},
onShadowChange (ids, names) {

},
getLocation(){
let _this = this;
const geolocation = new BMap.Geolocation();
geolocation.getCurrentPosition(function getinfo(position){
let city = position.address.city;
_this.LocationCity = city;
setStore("LocationCity",city);
setStore("latitude",position.latitude);
setStore("longitude",position.longitude);
console.log(position);
}, function(e) {
_this.LocationCity = "定位失败"

}, {provider: 'baidu'});
},
chooCity(){
this.cityPickerShow = !this.cityPickerShow
},
close(res){
if(res == 'close'){
this.chooCity();
}
},
toHome(){
this.$router.push("/home");
},
formPicker(res){
this.LocationCity = res;
},
nextStep(){
let latitude = getStore("latitude");
let longitude = getStore("longitude");
if(longitude == '' || latitude == ''){
this.$vux.toast.show({
text: '请选择考试城市',
type:'text',
position: 'middle'
})
}else if(this.exactive == ''){
this.$vux.toast.show({
text: '请选择工种',
type:'text',
position: 'middle'
})
}else{
setStore("active",this.active);
setStore("exactive",this.exactive);
this.$router.replace({path:'/organ',query:{id:this.gzid}});

}
},
},
mounted(){
showlist().then(res=>{
this.gzlists =res.data;
console.log(this.gzlists);

});
let LocationCity = getStore("LocationCity");
if(!LocationCity){
this.getLocation();
}else{
this.LocationCity = LocationCity;
}
}
}



