
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import {pay} from 'src/service/api'
export default {
data () {
return {
commonList: [ '微信', '支付宝'],
radioValue: ['微信'],
price:'',
order_sn:'',
minutes: 15,
seconds: 0,
money:'',
}
},
components: {
XHeader,Group, Cell,Checklist
},

created(){
this.money = JSON.parse(localStorage.getItem('hou'));
console.log(this.money.money);
this.order_sn = this.$route.query.order_sn;
console.log(this.order_sn);

},
mounted(){
console.log(11111111);
this.add()
},
methods: {
change (val, label) {
console.log(label);
this.payType = label;
},

















tijiao(){
let _this = this;
if(this.payType == "支付宝"){
pay({orderSN:this.order_sn}) .then((res) => {
console.log(res)
this.html = res
var form= res;

const div = document.createElement('div')
div.innerHTML = form
document.body.appendChild(div)
document.forms[0].submit()

})
}else if(this.payType == "微信"){
this.$vux.toast.show({
text: '暂未开通微信支付',
type:'text',
position: 'middle'
})
}

},
num: function (n) {
return n < 10 ? '0' + n : '' + n
},
add: function () {
var _this = this
var time = setInterval(function () {
if (_this.seconds === 0 && _this.minutes !== 0) {
_this.seconds = 59
_this.minutes -= 1
} else if (_this.minutes === 0 && _this.seconds === 0) {
_this.seconds = 0
clearInterval(time)
} else {
_this.seconds -= 1
}
}, 1000)
}
},
watch:{
second(val) {
this.num(val);
},
minute(val){
this.num(val);
}
},
computed: {
second: function () {
return this.num(this.seconds)
},
minute: function () {
return this.num(this.minutes)
}
}
}




