
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import footNav from 'src/components/footNav'
import {loginCheck} from 'src/service/api'
import { getStore } from 'src/config/mUtils'
export default {
components: {
XHeader,
Flexbox,
FlexboxItem,
Swiper,
SwiperItem,
footNav,
Toast
},
data () {
return {
borderColor: {
borderColor: '#333'
},
username:''
}
},
methods:{
back(){
this.$router.go(-1);
},
toCourse(){
this.$router.push("/courseList");
},
toCourse1(){
this.$router.push("/courseDetail");
},
toTeacher(){
this.$router.push("/teacherAll");
},
toTest(){
this.$router.push("/test");
},
toSc(){
this.$router.push('/sc')
},
toCt(){
this.$router.push('/ct')
},
toMyResult(){
this.$router.push('/myresult')
},
toStxue(){
this.$router.push('/stxue')
},
toStepTwo(){
loginCheck({username:this.username}).then(res=>{
let data=res.data;
console.log(res);
if(res.code == 0){
this.$vux.toast.show({
text: '请先登录',
type:'text',
position: 'middle'
})
setTimeout(()=>{
this.$router.push({path:'/login'});
},1000)
}else if(res.code == 1){
this.$router.push({path:'/mycourse'});
}
});
},
},
mounted(){
this.username = getStore("user");
}
}



